
<template>
    <div class="teacher">
        <MyHeader :pageName="title" ></MyHeader>
    <div style="height: 200px;">
     
      <!--   -->
        <div class="message">
          <mt-swipe class="mySwipe" :auto="0" :continuous="false">
            <mt-swipe-item>
                <img src="./teacher.jpeg" class="photo">
              <h3 style="color:#FFFFFF"></h3>
              <h5 style="color:#c7c7c7">web</h5>
            </mt-swipe-item>
            <mt-swipe-item>
                <h4 style="color: rgb(232, 232, 232)"></h4>
                <p style="color: rgb(232, 232, 232);padding-top: 15px;">
                    SassCompass10SassCompass
              </p>
            </mt-swipe-item>
          </mt-swipe>    
      </div>
      <!--  -->
      <div class="black"></div>
      <img src='./teacher.jpeg' onerror="this.style.display='none'" width="100%" height="200px;">
    <!--  -->
    </div>
    <div class="course">
        <div class="coursesCell" v-for="(course,index) in libraries">
          <!-- <div class="no" v-if="!course.isDefault" @click="pop"></div> -->
          <router-link :to=" '/course/' + index " class="link">
            <div class="imgcontainer">
              <img class="image" :src="course.cover" width="50px" height="50px" >
            </div>
            <span class="courseName">{{course.name}}</span>
            <p class="alt">({{course.docs.length}})</p>
          </router-link>
        </div>
    </div>
    </div>
</template>

<script>
  import MyHeader from '../header/Header.vue'
  import { Swipe, SwipeItem } from 'mint-ui';
  import { Indicator } from 'mint-ui';
  import { Toast } from 'mint-ui';
  export default {
    name: 'Teacher',
    data(){
      return {
        id: this.$route.params.id,
        libraries:[],
        title:''
      }
    },
    created(){
        // console.log(this.$route.params.id);
       this.pageInite();
    },
  methods:{
    pageInite(){
      Indicator.open();
      this.$http.get('/accounts/docs')
        .then((res) => {
          this.libraries = res.data.libraries;
          console.log(this.libraries,'lib');
          Indicator.close();

       })
    },
    pop(){
      Toast({
        message: '',
        iconClass: 'icon',
        position: 'bottom'
      });
    }
  },
      components:{
          MyHeader,
          'mt-swipe': Swipe,
          'mt-swipe-item': SwipeItem
      }
  }
</script>

<style>
    .teacher{
        margin-top: 40px;
    }
   .black{
    width: 100%;
    height: 200px;
    position: absolute;
    background-color:rgba(0, 0, 0, 0.48);
   /* -webkit-filter: blur(10px);
    -moz-filter: blur(10px);
    -o-filter: blur(10px);
    -ms-filter: blur(10px);
    filter: blur(10px);*/
    z-index: 1;
  }
   .message{
    width: 100%;
    height: 200px;
    position: absolute;
    top: 40px;
    z-index: 3;
  }

  .photo{
      width: 70px;
      height: 70px;
      border-radius: 50%;
      border: 1px solid lightgray;
      margin-top: 10px;
  }
  .mySwipe{
      width: 100%;
      height: 200px;
  }
  .coursesCell{
    width: 100%;
    background-color: rgba(212, 212, 212, 0.24);
    height: 100px;
    margin-top: 10px;
    /*margin-left: 5%;*/
    border-radius: 5px;
  }
  .link{
    display: flex;
    align-items: center;
    height: 100px;
    text-align: left;
  }
  .noclick{
    pointer-events: none;
  }
  .imgcontainer{
    display: inline-block;
    width: 48px;
    height: 48px;
    overflow: hidden;
    margin: 10px;
    border: 1px solid gray;
  }
  .image{
    display: inline-block;
    background-position: 0px 1px;
    margin: -1px;
  }
  .courseName{
    display: inline-block;
    width: 180px;
    text-overflow: ellipsis;
    overflow:hidden;
    white-space: nowrap;
    font-size: 18px;
    padding-left: 20px;
    color: black;
  }
  .alt{
    color: gray
  }
</style>



